<template>
  <div class="container">

    <div class="top">
      <input class="filter" v-model="filter" placeholder="Filtrer les épisodes" />
      <div class="icon-search">
        <svg aria-hidden="true" viewBox="0 0 512 512"><path fill="black" d="M505 442.7L405.3 343c-4.5-4.5-10.6-7-17-7H372c27.6-35.3 44-79.7 44-128C416 93.1 322.9 0 208 0S0 93.1 0 208s93.1 208 208 208c48.3 0 92.7-16.4 128-44v16.3c0 6.4 2.5 12.5 7 17l99.7 99.7c9.4 9.4 24.6 9.4 33.9 0l28.3-28.3c9.4-9.4 9.4-24.6.1-34zM208 336c-70.7 0-128-57.2-128-128 0-70.7 57.2-128 128-128 70.7 0 128 57.2 128 128 0 70.7-57.2 128-128 128z" class=""></path></svg>
      </div>
    </div>

    <ul class="episodes">
      <li v-for="(episode, index) in filteredEpisodes"
          :key="episode.name"
          :data-pair="index % 2 === 0"
          :data-current="episode === currentEpisode"
          class="episode"
          :ref="episode.name"
          @click="selectEpisode(episode, true)">
        {{episode.name}}
      </li>
    </ul>
    <div class="no-episodes"
          v-if="filteredEpisodes.length === 0">
      Aucun épisode trouvé.<br />
      Veuillez vider le filtre pour continuer.
    </div>
  </div>
</template>

<script>
import episodes from '@/services/episodes.js'
export default {
  name: 'episodes',
  props: ['currentEpisode'],
  data () {
    return {
      episodes: episodes.list(),
      filter: ''
    }
  },
  computed: {
    filteredEpisodes () {
      const lowerCaseFilter = this.filter.toLowerCase()
      return this.episodes.filter((ep) => {
        return ep.name.toLowerCase().indexOf(lowerCaseFilter) > -1
      })
    }
  },
  methods: {
    initWithEpisodeId (episodeId) {
      const ep = this.episodes.find((episode) => {
        return episode.name === episodeId
      })
      if (ep) {
        this.selectEpisode(ep)
      }
    },
    selectEpisode (episode, noScroll) {
      if (this.filteredEpisodes.length === 0) {
        return
      }
      this.$emit('selectEpisode', episode)
      if (!noScroll) {
        this.$refs[episode.name][0].scrollIntoView({ block: 'center' })
      }
      this.$router.push({ name: 'episode', params: { episodeId: episode.name } })
      document.title = episode.name
    },
    next (isRandom) {
      this.prevnext(1, isRandom)
    },
    previous (isRandom) {
      this.prevnext(-1, isRandom)
    },
    prevnext (arg, isRandom) {
      if (this.filteredEpisodes.length === 0) {
        return
      }
      if (isRandom) {
        this.randomNext()
      } else if (this.currentEpisode) {
        const index = this.filteredEpisodes.indexOf(this.currentEpisode)
        var newIndex = index + arg
        if (newIndex < 0) {
          newIndex = this.filteredEpisodes.length + newIndex
        } else if (newIndex > this.filteredEpisodes.length - 1) {
          newIndex = newIndex - this.filteredEpisodes.length
        }
        this.selectEpisode(this.filteredEpisodes[newIndex])
      } else if (arg > 0) {
        this.selectEpisode(this.filteredEpisodes[0])
      } else if (arg < 0) {
        this.selectEpisode(this.filteredEpisodes[this.filteredEpisodes.length - 1])
      }
    },
    randomNext () {
      const randomIndex = Math.floor(Math.random() * this.filteredEpisodes.length)
      this.selectEpisode(this.filteredEpisodes[randomIndex])
    }
  }
}
</script>

<style scoped lang="scss">
@import '@/styles/variables.scss';
.top {
  position: fixed;
  top: 0;
  max-width: 500px;
  height: 45px;
  width: 100%;
  display: flex;
  align-items: center;
  background-color: $white;

  .icon-search {
    height: 100%;
    padding-right: 7px;
    display: flex;
    align-items: center;
    svg {
      height: 30px;
      path {
        fill: $violet;
      }
    }
  }
}

.filter {
  width: 100%;
  padding: 15px;
  border: none;
  outline: none;
  background-color: $white;
}

.episode {
  background-color: $darkviolet;
  color: $white;
  padding: 15px;
  cursor: pointer;

  &[data-pair] {
    background-color: $violet
  }

  &:hover {
    background-color: $darkerviolet;
  }

  &[data-current] {
    background-color: $red;
  }
}

.no-episodes {
  padding: 15px;
  text-align: center;
}
</style>
