export default {
  list () {
    return episodes.map((name) => {
      let category = null
      const categoryMatch = name.match(/\((.+?)\)/)
      if (categoryMatch) {
        category = categoryMatch[1]
      }

      return {
        name,
        category,
        path: `episodes/${name}.mp3`
      }
    })
  }
}
